import { _decorator, Component, Node } from 'cc';
import { dragon } from './cDragon';
import { Label } from 'cc';
const { ccclass, property } = _decorator;

const GAME_COMBINATIONS:any[] = [
    { 3: 2 },		// hand cards no fix line 0
    { 3: 3 },		// hand cards no fix line 1
    { 3: 5 },		// hand cards no fix line 2
    { 3: 10 },		// hand cards no fix line 3
    { 3: 25 },	    // hand cards no fix line 4
    { 3: 50 },	    // hand cards no fix line 5
    { 3: 100 },	    // hand cards no fix line 6
    { 3: 0 },	    // hand cards no fix line 7
];
@ccclass('dragonDetail')
export class dragonDetail extends Component {
    @property({ type: Node, tooltip: "图片" })
    public roleSpriteNode: Node = null!;
    @property({ type: Node, tooltip: "背景板" })
    public BGNode: Node = null!;
    @property({ type: Node, tooltip: "反向背景板" })
    public BGNode2: Node = null!;
    @property({ type: Node, tooltip: "中奖分数" })
    public numNode: Node = null!;
    @property({ type: Node, tooltip: "特殊牌" })
    public specialNode: Node = null!;

    init (idx: any, isLeft: any) {
        if (dragon.detail) { 
            dragon.detail.node.active = false; 
        } 
        dragon.detail = this; 
        let id = idx; 
        this.roleSpriteNode.children[idx - 1].active = true; 
        for (let i in GAME_COMBINATIONS[id - 1]) { 
            (this.numNode.children[1].getComponent(Label) as Label).string = GAME_COMBINATIONS[idx - 1][i]; 
        } 
        if (idx == 7) { 
            this.BGNode.w = 540 
            this.BGNode2.w = 540 
            this.BGNode.h = 280 
            this.BGNode2.h = 280 
            if (!isLeft) { 
                this.BGNode.active = false; 
                this.BGNode2.active = true; 
                this.BGNode.x = -85 
                this.BGNode2.x = -340 
                this.numNode.x = -250; 
                this.specialNode.x = -340; 
                this.roleSpriteNode.x = -40 
            } else { 
                this.BGNode.x = -130 
                this.BGNode2.x = -270 
                this.numNode.x = 230; 
                this.specialNode.x = 100; 
                this.roleSpriteNode.x = -21 
            } 
        } else { 
            this.BGNode.w = 428 
            this.BGNode2.w = 428 
            this.BGNode.h = 238 
            this.BGNode2.h = 238 
            if (!isLeft) { 
                this.BGNode.x = -105 
                this.BGNode2.x = -260 
                this.BGNode.active = false; 
                this.BGNode2.active = true; 
                this.numNode.x = -180; 
                this.specialNode.x = -340; 
                this.roleSpriteNode.x = -40 
            } else { 
                this.BGNode.x = -110 
                this.BGNode2.x = -280 
                this.numNode.x = 160; 
                this.specialNode.x = 100; 
                this.roleSpriteNode.x = -21 
            } 
        } 
    }

    node_onclick () {
        dragon.node.deskMask.active = false; 
        this.node.destroy(); 
        dragon.detail = null; 
    }

}