import { _decorator, Component, AudioClip } from 'cc';
import { ModuleDef } from '../../scripts/ModuleDef';
const { ccclass, property } = _decorator;

@ccclass('dragonAudio')
export class dragonAudio extends Component {
    @property(AudioClip)
    public stopWheel = null;
    @property(AudioClip)
    public bigWinEf = null;
    @property(AudioClip)
    public clickstart = null;

    onLoad () {
     
    }

    start () {
        this.playBgm(0); 
    }

    stopbigwinmain () {
        this.playBgm(0); 
    }

    stopAudio() {
        tgx.AudioMgr.inst.audio.stopAll();
    }

    private urlArr:string[] = ["sound/bgm_mg_loop","sound/bgm_fs_loop","sound/bgm_bigwin"];
    private url: string = "audio/bgm_mg_loop";
    playBgm (index:number) {
        // AudioMgr.inst.stop();
        // AudioMgr.inst.playMusicLoop(this.urlArr[index],EGameName.dragon);
        tgx.AudioMgr.inst.audio.playMusicLoop(this.url, ModuleDef.SLOTS_DRAGON)
    }

    playclickopen () {
    }

    playclickclose () {
    }

    playStopWheel () {
        this.playEf(this.stopWheel); 
    }

    playBW () {
        this.playEf(this.bigWinEf); 
    }

    playEf (clip: any) {
        // AudioMgr.inst.playEffect(clip);
        tgx.AudioMgr.inst.audio.playEffect(clip);
    }

    playclickstart () {
        this.playEf(this.clickstart); 
    }

    clearAll(){
        tgx.AudioMgr.inst.audio.clearAll();
    }
}