import { Sprite } from 'cc';
import { math } from 'cc';
import { SpriteFrame } from 'cc';
import { _decorator, Component } from 'cc';
import { ccenum } from 'cc';
const { ccclass, property } = _decorator;
enum Type{
    one=2,
    two=5,
    three=10
}
ccenum(Type)
@ccclass('discoNum')
export class discoNum extends Component {
    @property({ type: [SpriteFrame], tooltip: "数字皮肤" })
    public spArr: SpriteFrame[] = [];
    @property({ type: Sprite, tooltip: "数字" })
    public numSp: Sprite = null!;

    @property({ type: Type, tooltip: "默认设置" })
    public defaultNum: Type = Type.one;

    private numArr:number[] = [2,5,10]
    onLoad () {
      
    }
    start(){
        if(!this.num){
            this.num = this.defaultNum;
        }
    }
    private _num:number=0;
    
    public get num() : number {
        return this._num;
    }
    
    public set num(v : number) {
        this._num = v;
        let index:number = this.numArr.indexOf(this.num);
        if(index==-1){
            console.error("Error:discoNum num");
        }
        this.numSp.spriteFrame = this.spArr[index];
    }

    radomNum(){
        let index:number = this.getRandomInt(0,this.numArr.length,1);
        this.num = this.numArr[index];
    }

    getRandomInt(min: number, max: number, type: number = 2): number {
        min = Math.ceil(min);
        max = Math.floor(max);
        switch (type) {
            case 1: // [min,max) 得到一个两数之间的随机整数,这个值不小于min（如果min不是整数的话，得到一个向上取整的 min），并且小于（但不等于）max  
                return Math.floor(math.random() * (max - min)) + min;
            case 2: // [min,max] 得到一个两数之间的随机整数，包括两个数在内,这个值比min大（如果min不是整数，那就不小于比min大的整数），但小于（但不等于）max
                return Math.floor(math.random() * (max - min + 1)) + min;
            case 3: // (min,max) 得到一个两数之间的随机整数
                return Math.floor(math.random() * (max - min - 1)) + min + 1;
        }
        return 0;
    }
}
