import { _decorator, Component } from 'cc';
import { dragon } from './cDragon';
import { tween } from 'cc';
import { Vec3 } from 'cc';
import { instantiate } from 'cc';
import { Animation } from 'cc';
import { TweenSystem } from 'cc';
import { Button } from 'cc';
import { UITransform } from 'cc';
import { EventTouch } from 'cc';
import { Node } from 'cc';
import { dragonDetail } from './dragonDetail';
import { Layout } from 'cc';
const { ccclass, property } = _decorator;

const ROLENUM = 20; //每一轮的角色数量
const TIMEMIN = 1; //第一轮摇奖时间
let W_INTERVAL = 0.1;
const FIRSTROLE = [//进入游戏时每列的角色
    [1, 1, 6, 6, 3],
    [1, 1, 7, 7, 7],
    [1, 1, 2, 5, 5],
];
@ccclass('dragonWheel')
export class dragonWheel extends Component {
    @property
    public wheelId = 0;
    @property
    public excludeId = 0;
    public status: number = 0;
    private nextCb: any;
    public rolePbList: any[] = [];
    public roleIdList: any[] = [];
    public lastResult: any[] = [];
    private isBlur: boolean = false;
    onLoad() {
        dragon.wheelList[this.wheelId] = this;
        this.status = 0; //0停止 1转  
        this.lastResult = [0, 0, 0]; //中间三位是上一局结果 首位末尾是为了防止露出部分转起来不一样  
        this.nextCb = null;//下一步需要执行的回调 
        this.rolePbList = []; //roles 
        this.roleIdList = []; //role ID 
        this.isBlur = false;
    }

    start() {
        this.initWheel();
    }

    initWheel() {
        if (dragon.isNewUser) {
            this.lastResult = FIRSTROLE[this.wheelId]; //中间三位是上一局结果 首位末尾是为了防止露出部分转起来不一样  
        } else {
            this.lastResult = [0, 0, 0, 0, 0];
        }
        this.rolePbList = [];
        this.roleIdList = [];
        this.node.removeAllChildren();
        for (let i in this.lastResult) {
            if (dragon.isNewUser) {
                this.addRole(this.lastResult[i]);
            } else {
                this.lastResult[i] = this.getRandomId();
                this.addRole(this.lastResult[i]);
            }
        }
        this.node.y = 0;
        dragon.rollIndex++;
        dragon.node.closeShine();
    }

    startRoll(...args: any) {
        for (let i = 0; i < args.length; i++) {
            this.addRole(args[i], 0);
        }
        this.addRole(this.getRandomId(), 0);
        if (dragon.slotCtrl.isSpeed) {
            setTimeout(() => {
                this.stopImmediately();
            }, TIMEMIN * 3 / 4 * 1000);
        }
        let cb = () => {
            this.addRole(this.getRandomId());
            let timeNum = TIMEMIN * 0.68;
            if (this.wheelId == 1) {
                this.addRole(this.getRandomId());
                this.addRole(this.getRandomId());
                this.addRole(this.getRandomId());
                timeNum += 0.2;
            }
            if (this.wheelId == 2) {
                this.addRole(this.getRandomId());
                this.addRole(this.getRandomId());
                this.addRole(this.getRandomId());
                this.addRole(this.getRandomId());
                this.addRole(this.getRandomId());
                this.addRole(this.getRandomId());
                timeNum += 0.4;
            }
            for (let i in args) {
                this.addRole(args[i]);
            }
            this.addRole(this.getRandomId());
            (this.node.getComponent(Layout) as Layout).updateLayout();
            tween(this.node)
                .to(timeNum, { y: (-this.node.size.height + 750) })//, { easing: 'quadOut' }
                .call(() => {
                    this.isBlur = false;
                    this.setBlur();
                })
                .to(0.3, { y: (-this.node.size.height + 800) })
                .call(() => {
                    this.rollCallBack();
                })
                .start()
        };
        this.nextCb = cb;
    }

    rollLoop() {
        this.status = 1;
        this.rolePbList = [];
        this.roleIdList = [];
        this.node.removeAllChildren();
        this.node.y = 0; //滚轮启动位移距离 
        for (let i = 0; i < this.lastResult.length; i++) {
            this.lastResult[i] = this.lastResult[i] == 0 ? this.getRandomId() : this.lastResult[i];
            this.addRole(this.lastResult[i]);
        }
        for (let i = 0; i < ROLENUM - 6; i++) {
            this.addRole(this.getRandomId());
        }
        for (let i = 0; i < this.lastResult.length; i++) {
            this.lastResult[i] = this.lastResult[i] == 0 ? this.getRandomId() : this.lastResult[i];
            this.addRole(this.lastResult[i]);
        }
        let loopCb = () => {
            this.node.y = 0; //滚轮启动位移距离 
            let timer = TIMEMIN + this.wheelId * 0.2;
            if (dragon.slotCtrl.isSpeed) {
                tween(this.node)
                    .call(() => {
                        this.isBlur = true;
                        this.setBlur();
                    })
                    .to(TIMEMIN, { y: -this.node.h + 800 })//, { easing: 'quadInOut' }) 
                    .call(() => {
                        this.nextCb()
                    })
                    .start()
            } else {
                tween(this.node)
                    .call(() => {
                        this.isBlur = true;
                        this.setBlur();
                    })
                    .to(timer, { y: -this.node.h + 800 })//, { easing: 'quadInOut' }) 
                    .call(() => {
                        this.nextCb()
                    })
                    .start()
            }
        };
        setTimeout(() => {
            this.isBlur = true;
            this.setBlur();
            let timer = TIMEMIN + this.wheelId * 0.2;
            if (dragon.slotCtrl.isSpeed) {
                tween(this.node)
                    .to(0.16, { y: 30 })
                    .call(() => {
                        this.isBlur = true;
                        this.setBlur();
                    })
                    .to(TIMEMIN * 3 / 4, { y: -this.node.h + 800 })
                    .call(() => {
                        this.nextCb()
                    })
                    .start()
            } else {
                tween(this.node)
                    .delay(this.wheelId * W_INTERVAL)
                    .to(0.16, { y: 30 })
                    .call(() => {
                        this.isBlur = true;
                        this.setBlur();
                    })
                    .to(TIMEMIN, { y: -this.node.h + 800 })
                    .call(() => {
                        this.nextCb()
                    })
                    .start()
            }
        }, 100);
        this.nextCb = loopCb;
    }

    getRandomId() {
        let randomId = 0;
        while (randomId == 0) {
            randomId = Math.floor(Math.random() * (dragon.node.rolePb.length - 1) + 1);
            randomId = randomId == this.excludeId ? 0 : randomId;
        };
        return randomId;
    }

    addRole(id: any, posId: any = undefined) {
        let pb = instantiate(dragon.node.rolePb[id]);
        if (posId !== undefined) {
            this.rolePbList.unshift(pb);
            this.roleIdList.unshift(id);
        } else {
            this.rolePbList.push(pb);
            this.roleIdList.push(id);
        }
        this.addClickEvent(pb, id);
        this.node.addChild(pb);
        if (posId !== undefined) {
            pb.setSiblingIndex(posId);
        }
        this.setBlur(pb)
    }

    rollCallBack() {
        this.isBlur = false;
        this.node.children[0].opacity = 0;
        this.node.children[4].opacity = 0;
        this.setBlur();
        dragon.node.audio.playStopWheel();
        this.lastResult = this.roleIdList.slice(1, 4);
        this.status = 0;
        dragon.node.stateCallBack();
        for (let i = 0; i < dragon.node.effectSpinLightArr.length; i++) {
            dragon.node.effectSpinLightArr[i].getComponent(Animation)?.stop();
            dragon.node.effectSpinLightArr[i].opacity = 0;
        }
    }

    stopImmediately() {
        this.isBlur = false;
        this.setBlur();
        this.lastResult = this.roleIdList.slice(1, 4);
        TweenSystem.instance.ActionManager.removeAllActionsFromTarget(this.node);
        setTimeout(() => {
            this.node.y = -this.node.h + 800;
        }, 50);
        this.status = 0;
        dragon.node.stateCallBack();
        for (let i = 0; i < dragon.node.effectSpinLightArr.length; i++) {
            dragon.node.effectSpinLightArr[i].getComponent(Animation)?.stop();
            dragon.node.effectSpinLightArr[i].opacity = 0;
        }
    }

    addClickEvent(node: any, idx: any) {
        let clickEventHandler = new Component.EventHandler();
        clickEventHandler.target = this.node; // 这个 node 节点是你的事件处理代码组件所属的节点
        clickEventHandler.component = "dragonWheel";// 这个是代码文件名 
        clickEventHandler.handler = "roleClick";
        clickEventHandler.customEventData = idx;
        let button = node.getComponent(Button);
        button.clickEvents.push(clickEventHandler);
    }

    roleClick(event: EventTouch, customEventData: any) {
        let newNode = instantiate(dragon.node.detailPb);
        let transForm: UITransform = (event.target as Node).getComponent(UITransform) as UITransform;
        let newVec3: Vec3 = transForm.convertToWorldSpaceAR(new Vec3(0, 0, 0));
        transForm = dragon.node.getComponent(UITransform) as UITransform;
        let p = transForm.convertToNodeSpaceAR(newVec3);
        newNode.position = p;
        newNode.x -= 30;
        let isLeft = newNode.x < 10;
        (newNode.getComponent("dragonDetail") as dragonDetail).init(customEventData, isLeft);
        dragon.node.node.addChild(newNode);
        dragon.node.deskMask.active = true;
    }

    setBlur(node: any = undefined) {
        if (node) {
            this.setNodeBlur(node)
        } else {
            for (var i in this.rolePbList) {
                var node = this.rolePbList[i]
                this.setNodeBlur(node);
            }
        }
    }

    setNodeBlur(node: any) {
        if (this.isBlur) {
            node.children[0].active = false;
            node.children[1].active = true;
        } else {
            node.children[0].active = true;
            node.children[1].active = false;
        }
    }

}