import { Prefab } from 'cc';
import { Node } from 'cc';
import { _decorator, Component } from 'cc';
import { discoNum } from './discoNum';
import { instantiate } from 'cc';
import { Vec3 } from 'cc';
import { tween } from 'cc';
import { Layout } from 'cc';
import { Tween } from 'cc';
import { dragon } from './cDragon';
const { ccclass, property } = _decorator;

@ccclass('discoRoll')
export class discoRoll extends Component {
    @property({ type: Prefab, tooltip: "数字皮肤" })
    public pb: Prefab = null!;

    private MaxSize: number = 1;
    private MainSize: number = 0.7;
    private rollTime: number = 1;
    private radomNum: number = 10;
    onLoad() {

    }

    private startX: number = -114;
    private showRange: number = 83;

    private aimDis: number = 145;
    start() {
        this.startX = this.node.x;
        this.rollBn = false;
    }

    protected update(dt: number): void {
        if (!this.rollBn) {
            return;
        }
        this.updateSize();
    }

    showBn(item: Node): boolean {
        let bn: boolean = false;
        let parent: Node = item.parent as Node;
        let relativePos: Vec3 = new Vec3(item.position.x + parent.position.x, item.position.y + parent.position.y);
        let boundaryL: number = relativePos.x - item.w / 2;
        let boundaryR: number = relativePos.x + item.w / 2;
        if (boundaryL < this.showRange && boundaryR > -this.showRange) {
            bn = true;
        }
        return bn;
    }

    showSize(item: Node): number {
        let size: number = 0;
        let parent: Node = item.parent as Node;
        let relativePos: Vec3 = new Vec3(item.position.x + parent.position.x, item.position.y + parent.position.y);
        let disx: number = this.showRange - Math.abs(relativePos.x);
        if (disx < 0) {
            size = this.MainSize;
        } else {
            let dis: number = this.MaxSize - this.MainSize;
            let a: number = Math.acos(Math.abs(relativePos.x) / this.showRange);
            size = this.MainSize + dis * Math.sin(a);
        }
        return size;
    }

    private rollBn: boolean = false;
    startRoll() {
        this.rollBn = true;
        this.rollLoop();
    }

    private aimNum: number = 0;
    endRoll(num: number = 0) {
        this.aimNum = num;
        Tween.stopAllByTarget(this.node);
        this.rollEnd();
    }

    private radomAdd() {
        this.updateSize(true);
        let addnum: number = this.radomNum - this.node.children.length;
        this.create(addnum);
        (this.node.getComponent(Layout) as Layout).updateLayout();
    }

    private updateSize(isRecycle: boolean = false) {
        for (let i: number = this.node.children.length - 1; i >= 0; i--) {
            let item: Node = this.node.children[i];
            let showBn: boolean = this.showBn(item);
            if (showBn) {
                let size: number = this.showSize(item);
                item.scale = new Vec3(size, size);
                item.opacity = 255;
            } else {
                isRecycle && this.recycle(item);
            }
        }
    }

    private rollLoop() {
        this.radomAdd();
        this.node.x = this.startX; //滚轮启动位移距离 
        let timer = this.rollTime;
        tween(this.node)
            .to(timer, { x: this.startX - this.node.w + 230 })//, { easing: 'quadInOut' }) 
            .call(() => {
                this.rollLoop()
            })
            .start()
    }

    private rollEnd() {
        this.radomAdd();
        let aim: number = 0;
        if (this.aimNum != 0) {
            let last: discoNum = this.node.children[this.node.children.length - 1].getComponent(discoNum) as discoNum;
            last.num = this.aimNum;
            aim = this.startX - this.node.w + this.aimDis;
        } else {
            aim = this.startX - this.node.w + 230;
        }
        this.node.x = this.startX; //滚轮启动位移距离 
        let timer = this.rollTime;
        tween(this.node)
            .to(timer, { x: aim })//, { easing: 'quadInOut' }) 
            .call(() => {
                this.endComplete()
            })
            .start()
    }

    private endComplete() {
        this.rollBn = false;
        if (this.aimNum != 0) {
            let node:Node = this.node.children[this.node.children.length-1];
            tween(node)
                .to(0.3,{y:node.y+8,scale:new Vec3(0.8,0.8)})
                .delay(0.2)
                .to(0.2,{y:node.y,scale:new Vec3(0.7,0.7)})
                .start();
            dragon.node.discoNumFly(this.node.children[this.node.children.length - 1],this.pb);
        }
    }

    flyUp(callBack:Function){
        let item:Node = this.node.children[this.node.children.length-1];
        if(!item) return;
        tween(item)
            .to(0.5,{y:item.position.y+50})
            .call(()=>{
                callBack();
                this.flyDown();
            })
            .start();
    }

    flyDown(){
        let item:Node = this.node.children[this.node.children.length-1];
        if(!item) return;
        tween(item)
            .to(0.5,{y:item.position.y-50,opacity:100})
            .start();
    }

    aimBack(){
        let item:Node = this.node.children[this.node.children.length-1];
        if(!item) return;
        item.opacity = 255;
    }

    private pool: Node[] = [];
    private create(num: number) {
        for (let i: number = 0; i < num; i++) {
            this.createOne();
        }
    }

    private createOne() {
        let item: discoNum = this.itemFactory().getComponent(discoNum) as discoNum;
        item.node.scale = new Vec3(this.MainSize, this.MainSize);
        item.radomNum();
        this.node.addChild(item.node);
    }

    private itemFactory(): Node {
        if (this.pool.length > 0) {
            return this.pool.shift() as Node;
        } else {
            return instantiate(this.pb);
        }
    }

    private recycle(item: Node) {
        item.parent = null;
        this.pool.push(item);
    }

    private _isSpeed:boolean = false;
    
    public get isSpeed() : boolean {
        return this._isSpeed;
    }

    public set isSpeed(v : boolean) {
        this._isSpeed = v;
        if(this.isSpeed){
            this.radomNum = 5;
        }else{
            this.radomNum = 10;
        }
    }
}
