import { tween } from "cc";
import { Component, _decorator } from "cc";
import { dragon } from "./cDragon";
import { Node } from "cc";
import { Label } from "cc";
import { Color } from "cc";
import { EventTouch } from "cc";
import { Widget } from "cc";
const { ccclass, property } = _decorator;

@ccclass('rulePanel')
export class rulePanel extends Component {
    @property({ type: Node, tooltip: "" })
    public panel: Node = null!;
   
    init() {
        dragon.rulePanel = this;
        this.node.parent.children[0].active = false;
        this.node.active = false;
    }

    start() {
        this.init();
    }

    private showPos: number;
    private hidePos: number;
    showPanel() {
        dragon.node.audio.playclickopen();
        this.node.parent.children[0].active = true;
        this.node.active = true;
        this.panel.w = this.node.w;
        this.panel.h = this.node.h;
        const widgets = this.panel.getComponentsInChildren(Widget);
        for (let i = 0; i < widgets.length; i++) {
            widgets[i].updateAlignment();
        }
        this.showPos = 0;
        this.hidePos = -this.panel.h;
        this.panel.y = this.hidePos;
        tween(this.panel)
            .to(0.2, { y: this.showPos })
            .start()
        tween(this.node)
            .delay(0.2)
            .call(() => {
                tween(this.panel.children[0])
                    .to(0.2, { opacity: 255 })
                    .start()
            })
            .start()
    }

    hidePanel() {
        tween(this.panel)
            .to(0.2, { y: this.hidePos })
            .call(() => {
                this.node.parent.children[0].active = false;
                this.node.active = false;
            })
            .start()
    }

    onClose() {
        dragon.node.audio.playclickclose();
        this.hidePanel();
    }
}
