
import { Prefab } from 'cc';
import { Node } from 'cc';
import { _decorator, Component } from 'cc';
import { discoNum } from './discoNum';
import { instantiate } from 'cc';
import { Vec3 } from 'cc';
import { tween } from 'cc';
import { Layout } from 'cc';
import { Tween } from 'cc';
import { dragon } from './cDragon';
const { ccclass, property } = _decorator;

@ccclass('topRoll')
export class topRoll extends Component {
    @property({ type: Prefab, tooltip: "数字皮肤" })
    public pb: Prefab = null!;
    @property({ type: Node, tooltip: "光效" })
    public lightEffect: Node = null!;
    @property({ type: Node, tooltip: "黑色半透明" })
    public black: Node = null!;

    private rollTime: number = 1;
    private radomNum: number = 10;
    onLoad() {

    }

    private startX: number = -230;
    private showRange: number = 206;

    private aimDis: number = 125;
    start() {
        this.startX = this.node.x;
        this.rollBn = false;
        this.black.active = false;
    }

    showBn(item: Node): boolean {
        let bn: boolean = false;
        let parent: Node = item.parent as Node;
        let relativePos: Vec3 = new Vec3(item.position.x + parent.position.x, item.position.y + parent.position.y);
        let boundaryL: number = relativePos.x - item.w / 2;
        let boundaryR: number = relativePos.x + item.w / 2;
        if (boundaryL < this.showRange && boundaryR > -this.showRange) {
            bn = true;
        }
        return bn;
    }

    private rollBn: boolean = false;
    startRoll() {
        this.rollBn = true;
        this.black.active = false;
        this.rollLoop();
    }

    private aimNumArr: number[] = [];
    endRoll(numArr: number[] = []) {
        if(!numArr){
            this.aimNumArr = [];
        }else{
            this.aimNumArr = numArr;
        }
        Tween.stopAllByTarget(this.node);
        this.rollEnd();
    }

    private radomAdd() {
        this.updateRecycle(true);
        let addnum: number = this.radomNum - this.node.children.length;
        this.create(addnum);
        (this.node.getComponent(Layout) as Layout).updateLayout();
    }

    private updateRecycle(isRecycle: boolean = false) {
        for (let i: number = this.node.children.length - 1; i >= 0; i--) {
            let item: Node = this.node.children[i];
            let showBn: boolean = this.showBn(item);
            if (!showBn) {
                isRecycle && this.recycle(item);
            }
        }
    }

    private rollLoop() {
        this.radomAdd();
        this.node.x = this.startX; //滚轮启动位移距离 
        let timer = this.rollTime;
        tween(this.node)
            .to(timer, { x: this.startX - this.node.w + 230 })//, { easing: 'quadInOut' }) 
            .call(() => {
                this.rollLoop()
            })
            .start()
    }

    private flyNumArr:Node[] = [];
    private rollEnd() {
        this.radomAdd();
        let aim: number = 0;
        if (this.aimNumArr.length==3) {
            aim = this.startX - this.node.w + 3*this.aimDis+10;
            (this.node.children[this.node.children.length - 1].getComponent(discoNum) as discoNum).num = this.aimNumArr[2];
            (this.node.children[this.node.children.length - 2].getComponent(discoNum) as discoNum).num = this.aimNumArr[1];
            (this.node.children[this.node.children.length - 3].getComponent(discoNum) as discoNum).num = this.aimNumArr[0];
            this.flyNumArr = [this.node.children[this.node.children.length - 3],this.node.children[this.node.children.length - 2],this.node.children[this.node.children.length - 1]];
        } else if(this.aimNumArr.length==2) {
            aim = this.startX - this.node.w + 460;
            (this.node.children[this.node.children.length - 2].getComponent(discoNum) as discoNum).num = this.aimNumArr[1];
            (this.node.children[this.node.children.length - 3].getComponent(discoNum) as discoNum).num = this.aimNumArr[0];
            this.flyNumArr = [this.node.children[this.node.children.length - 3],this.node.children[this.node.children.length - 2]];
        }else{
            console.log("aimNumArr Error");
            return;
        }
        this.node.x = this.startX; //滚轮启动位移距离 
        let timer = this.rollTime;
        tween(this.node)
            .to(timer, { x: aim })//, { easing: 'quadInOut' }) 
            .call(() => {
                this.endComplete()
            })
            .start()
    }

    private endComplete() {
        this.rollBn = false;
        this.black.active = true;
        this.lightEffect.active = true;
        this.lightEffect.opacity = 255;
        tween(this.lightEffect)
            .delay(0.5)
            .to(0.3, { opacity: 0 })
            .start();
        dragon.node.topNumFly(this.flyNumArr,this.pb);
        for(let i:number = 0;i<this.flyNumArr.length;i++){
            let node:Node = this.flyNumArr[i];
            tween(node)
                .to(0.3,{y:node.y+20,scale:new Vec3(0.8,0.8)})
                .delay(0.2+i*0.1)
                .to(0.2,{y:node.y,scale:new Vec3(0.7,0.7)})
                .start();
        }
    }

    // flyUp(callBack:Function){
    //     let item:Node = this.node.children[this.node.children.length-1];
    //     if(!item) return;
    //     tween(item)
    //         .to(0.5,{y:item.position.y+50})
    //         .call(()=>{
    //             callBack();
    //             this.flyDown();
    //         })
    //         .start();
    // }

    // flyDown(){
    //     let item:Node = this.node.children[this.node.children.length-1];
    //     if(!item) return;
    //     tween(item)
    //         .to(0.5,{y:item.position.y-50,opacity:100})
    //         .start();
    // }

    aimBack(){
        let item:Node = this.node.children[this.node.children.length-1];
        if(!item) return;
        item.opacity = 255;
    }

    private pool: Node[] = [];
    private create(num: number) {
        for (let i: number = 0; i < num; i++) {
            this.createOne();
        }
    }

    private createOne() {
        let item: discoNum = this.itemFactory().getComponent(discoNum) as discoNum;
        item.node.scale = new Vec3(0.7, 0.7);
        item.radomNum();
        this.node.addChild(item.node);
    }

    private itemFactory(): Node {
        if (this.pool.length > 0) {
            return this.pool.shift() as Node;
        } else {
            return instantiate(this.pb);
        }
    }

    private recycle(item: Node) {
        item.parent = null;
        this.pool.push(item);
    }

    private _isSpeed:boolean = false;
    
    public get isSpeed() : boolean {
        return this._isSpeed;
    }

    public set isSpeed(v : boolean) {
        this._isSpeed = v;
        if(this.isSpeed){
            this.radomNum = 5;
        }else{
            this.radomNum = 10;
        }
    }
}
